// react
import React from "react";
import PropTypes from "prop-types";
// zerod
import { Zlayout, ZmainHOC } from "zerod";
import { BrowserRouter, HashRouter, Route } from "react-router-dom";
import { Menu, Dropdown, Icon } from "antd";
import HOC from "@/HOC/load-HOC.js";
const { AmdDocHOC, AshowDemoHOC } = HOC;
import Main from "@/views/Main";
import cssClass from "@/views/Main/style.scss";
import doc from "zerod/components/ZmainHOC/doc.md";
import publiced from "../../../../../config/pablic";
import logo from "@/assets/images/logo.png";

// 路由组件
import mainRoutes from "../../load-child-routes.js";

const Router = publiced.path == "/" ? BrowserRouter : HashRouter;

const showMain = function(Component) {
	return (
		<div style={{ height: "100%" }}>
			<Router>
				<Route path={`/main/HOC-doc/ZmainHOC-doc`} component={Component} />
			</Router>
		</div>
	);
};
export default AmdDocHOC(doc, {
	demo1: function() {
		const MyScript = AshowDemoHOC(showMain(Main), false);
		return <MyScript />;
	},
	demo2: function() {
		const pageConfig = {
			// 左侧边展开时的宽度
			leftExpandWidth: 280,
			// 主页的主题 light | dark
			theme: "dark",
			// 顶部栏左边内容的渲染钩子
			headerLeftRender: (main) => {
				return <Zlayout.ZheaderBtn className="z-margin-right-15">左边</Zlayout.ZheaderBtn>;
			},
			// 顶部栏右边内容的渲染钩子
			headerRightRender: (main) => {
				return (
					<Zlayout.Template>
						<Zlayout.ZheaderBtn className="z-margin-right-15">按钮1</Zlayout.ZheaderBtn>
						<Zlayout.ZheaderBtn className="z-margin-right-15">按钮2</Zlayout.ZheaderBtn>
						<UserDropdown />
					</Zlayout.Template>
				);
			},
			// 组件加载完成的钩子
			componentDidMount: (callback) => {
				callback();
			},
		};

		class UserDropdown extends React.PureComponent {
			methods = {
				//用户dropdown按钮点击触发
				onMenuClick: (item) => {
					// if (item.key === "/logout") {
					// 	window.location.assign(item.key);
					// } else {
					// }
				},
			};
			dropdownMenu = (
				<Menu
					className={cssClass["z-main-user-menu"]}
					selectedKeys={[]}
					onClick={this.methods.onMenuClick && this.methods.onMenuClick}
				>
					<Menu.Item disabled>
						<Icon type="user" />
						个人中心
					</Menu.Item>
					<Menu.Item disabled>
						<Icon type="setting" />
						设置
					</Menu.Item>
					<Menu.Divider />
					<Menu.Item key="/logout">
						<Icon type="logout" />
						退出登录
					</Menu.Item>
				</Menu>
			);
			render() {
				return (
					<Dropdown overlay={this.dropdownMenu} trigger={["click"]} placement="bottomRight">
						<Zlayout.ZheaderBtn className="z-margin-right-15">
							<span className="z-icon-circle z-margin-right-8">
								<Icon type="user" />
							</span>
							登录用户
						</Zlayout.ZheaderBtn>
					</Dropdown>
				);
			}
		}
		const NewMain = ZmainHOC(pageConfig);
		const MyScript = AshowDemoHOC(showMain(NewMain), false);
		return <MyScript />;
	},
	demo3: function() {
		class Logo extends React.PureComponent {
			static propTypes = {
				getLogoMethods: PropTypes.func,
			};
			state = {
				showTitle: true,
			};
			toggleTitle = (show) => {
				this.setState({
					showTitle: show,
				});
			};
			componentDidMount() {
				this.props.getLogoMethods &&
					this.props.getLogoMethods({
						toggleTitle: this.toggleTitle,
					});
			}
			render() {
				return (
					<div className="z-flex-items-v-center" style={{ height: "100%" }}>
						<img src={logo} alt="" width="32" className="z-margin-left-24" />
						{this.state.showTitle ? (
							<span
								className="z-margin-left-12 z-font-size-20"
								style={{ fontWeight: 600, color: "white" }}
							>
								Zero-design
							</span>
						) : null}
					</div>
				);
			}
		}
		class Main extends React.Component {
			componentDidMount() {}
			render() {
				//自定义主页布局，经过ZmainHOC包装的组件，会this.props.getSideMenuTemplate和this.props.getMaimRouteTemplate两个方法
				return (
					<Zlayout>
						<Zlayout.Zheader style={{ backgroundColor: "#0A1131" }}>
							<Logo />
						</Zlayout.Zheader>
						<Zlayout.Zbody scroll={false}>
							<Zlayout flexRow>
								<Zlayout width={"320px"}>
									{this.props.getSideMenuTemplate({
										theme: "dark",
										isCollapse: false,
										openAllSubmenu: true,
									})}
								</Zlayout>
								<Zlayout>{this.props.getMaimRouteTemplate("my_main_body")}</Zlayout>
							</Zlayout>
						</Zlayout.Zbody>
					</Zlayout>
				);
			}
		}
		const NewMain = ZmainHOC(Main, (callback) => {
			//同pageConfig的componentDidMount函数
			callback(
				//保存的用户信息
				{},
				//侧边导航数据
				[
					{
						permUrl: "HOC-doc",
						permName: "HOC",
						children: [
							{
								permUrl: "ZappHOC-doc",
								permName: "根组件 : ZappHOC",
								iconClass: "",
							},
							{
								permUrl: "ZmainHOC-doc",
								permName: "主页布局 : ZmainHOC",
								iconClass: "",
							},
							{
								permUrl: "ZpageWraperHOC-doc",
								permName: "页面头尾结构 : ZpageWrapper",
								iconClass: "",
							},
							{
								permUrl: "ZsearchListHOC-doc",
								permName: "列表展示 : ZsearchListHOC",
								iconClass: "",
							},
							{
								permUrl: "ZeditSimpleFormHOC-doc",
								permName: "编辑页面 : ZeditSimpleFormHOC",
								iconClass: "",
							},
							{
								permUrl: "ZdetailSimpleBaseHOC-doc",
								permName: "详情页面 : ZdetailSimpleBaseHOC",
								iconClass: "",
							},
							{
								permUrl: "ZeditorTreeHOC-doc",
								permName: "树列表页 : ZeditorTreeHOC",
								iconClass: "",
							},
						],
					},
				],
				//mapKeys
				{ iconClass: "permIconUrl", path: "permUrl", name: "permName", children: "children" },
				//路由配置数据
				mainRoutes,
			);
		});
		const MyScript = AshowDemoHOC(showMain(NewMain), false);
		return <MyScript />;
	},
});
